* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #0B2434;
}

.app {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 460px;
    max-width: 460px;
    border-radius: 10px;
    background-color: #F5F5F5;
    margin: 30px 20px;
    margin-right: auto;
    margin-left: auto;
}

.die {
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    width: 55px;
    height: 55px;
    border-radius: 5px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.dice-container {
    height: fit-content;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 20px;
}

button {
    width: 130px;
    height: 50px;
    margin-top: 40px;
    background: #5035FF;
    box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
    border-radius: 5px;
    border: none;
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    color: white;
    font-size: 19px;
    cursor: pointer;
}

.title {
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    font-size: 35px;
    margin: 0;
}

p {
    font-family: 'Inter', sans-serif;
    text-align: center;
    padding: 5px 70px;
    padding-bottom: 30px;
    margin: 0;
}

h4 {
    font-family: 'Karla', sans-serif;
    margin: 0;
    margin-top: 15px;
}

h5 {
    font-family: 'inter', sans-serif;
    font-weight: 400;
    margin: 0;
    margin-top: 10px;
}

span {
    color: #5035FF;
    font-weight: 700;
}